* {
    margin: 0;
    padding: 0;
}

body {
    background-color: rgb(204, 185, 185);
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 600px;
    width: 600px;
    border: 3px solid black;
    margin: 12px auto 0 auto;
    box-shadow: 12px 23px 16px 12px rgba(63, 18, 18, 0.25);
    background-color: wheat;
}

.news {
    font-size: 3rem;
    margin-top: 46px;
    text-decoration: underline;
}

.title {
    font-size: 1.4rem;
    margin-top: 12px;
}

.information {
    font-size: 1.2rem;
    margin-top: 8px;
}